.nsw-card {
  @include font-stack;
  @include border-radius;
  background-color: $white;
  box-shadow: map-get($shadows, card);
  transition: map-get($transitions, card);
  position: relative;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  height: 100%;
  border: 0;

  &:hover {
    box-shadow: map-get($shadows, card-hover);
  }

  &:focus-within {
    @include nsw-focus;

    .nsw-card__link {
      text-decoration: none;
    }
  }

  &__title {
    @include font-stack('heading');
    @include nsw-font-size('md');
    margin: 0;
  }

  &__content-container {
    border-top: solid 6px $nsw-primary-highlight;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    order: 2;

    @include nsw-spacing(padding, none none md none );
  }

  &__content {
    order: 2;

    @include nsw-font-size('sm');
    @include nsw-spacing(padding, md );

    @include breakpoint('lg') {
      @include nsw-spacing(padding, xl );
    }

    .nsw-card--content & {
      border-top: 0;
    }
  }

  &__copy {
    margin: 1rem 0 0;
  }

  &__tag {
    @include font-stack('heading');
    order: 1;
    margin: 0 0 1rem;

    + .nsw-card__date {
      margin-top: rem(-8);
    }
  }

  &__date {
    order: 2;
    margin: 0 0 1rem;
  }

  &__tag,
  &__date {
    @include nsw-font-size('xs');
  }

  &__title,
  &__copy,
  &__icon {
    order: 3;
  }

  &__button {
    order: 4;

    .nsw-button {
      margin-right: 0;
    }
  }

  &__icon {
    @include nsw-spacing(margin, md none none none);
    position: absolute;
    bottom: rem(13px);
    left: rem(13px);

    &.nsw-icon {
      @include nsw-spacing(bottom, md);
      width: rem(24px);
      height: rem(24px);
      left: auto;
    }

    @include breakpoint('lg') {
      @include nsw-spacing(margin, xl none none none);
      bottom: rem(29px);
      left: rem(28px);

      &.nsw-icon {
        @include nsw-spacing(bottom, xl);
      }
    }
  }

  &__link {
    @include pseudo-clickable-block;
    text-decoration: none;
    color: $nsw-primary-blue;

    &:hover {
      text-decoration: underline;
    }

    &:focus {
      outline: none;
      text-decoration: underline;
    }
  }

  &__image-area {
    order: 1;
    height: rem(200px);
  }

  &__image {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &-- {
    &headline {
      .nsw-card__title {
        @include nsw-font-size('lg');
      }
    }

    &transparent {
      background-color: transparent;
      box-shadow: none;
    }

    &text-white {
      &,
      .nsw-card__link {
        color: $white;
      }
    }

    &centered {
      text-align: center;
    }

    &border {
      &-white {
        @include border(null, md, 2px);
        @include border-radius(tag);
      }
    }

    &no-highlight {
      .nsw-card__content {
        border-top: 0;
      }

      .nsw-card__image-area img {
        @include border-radius;
      }
    }

    &small-font {
      &,
      .nsw-button,
      .nsw-card__content {
        font-size: 0.8rem;
      }
    }
  }
}

@each $color, $value in $bg-colors {
  .nsw-card--#{$color} {
    .nsw-card__content-container {
      border-top-color: $value;
    }
  }
}
