.gwic-skipto {
  @include font-stack;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;

  &__link {
    @include nsw-spacing(padding, xs md);
    @include z-index('middle');
    background-color: $nsw-primary-blue;
    color: $white;
    transform: translateY(-101%);
    display: block;
    text-align: center;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    text-decoration: none;

    &:focus,
    &:active {
      transform: translateY(0);
      outline: none;

      span {
        @include nsw-focus($color: $white);
      }
    }

    @include breakpoint('lg') {
      text-align: left;
    }
  }
}

.gwic-infobar,
.gwic-masthead {
  @include font-stack;
  @include nsw-spacing(padding, sm none);
  background-color: $gwic-sky-blue;
  color: $white;

  p {
    margin: 0;
    font-size: rem(12px);
  }
}

.gwic-header {
  background-color: $gwic-blue;
  position: relative;
  border-bottom: solid 1px $light20;

  @include breakpoint('lg') {
    border-bottom: 0;
  }

  &--simple {
    @include breakpoint('lg') {
      border-bottom: solid 1px $light40;
    }
  }

  &__container {
    @include container;
    position: relative;
  }

  &__wrapper {
    @include nsw-spacing(padding, sm none);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-content: center;
    align-items: center;
    height: rem(72px);
  }

  &__center {
    order: 2;

    @include breakpoint('lg') {
      order: 0;
    }
  }

  &__left {
    order: 1;
    margin-left: rem(-6px);
  }

  &__right {
    order: 3;
    display: flex;
  }

  &__logo-link {
    display: block;

    &:focus {
      @include nsw-focus;
    }
  }
}

.gwic-header__logo {
  width: rem(141px);
  height: rem(46px);
}

  /* stylelint-disable */
%gwic-menu-btn {
  @include border-radius;
  color: $white;
  width: rem(48px);
  height: rem(48px);
  background: none;
  border: 0;
  padding: 0;
  text-align: center;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  &:hover {
    @include nsw-hover;
  }

  &:focus {
    @include nsw-focus;
  }
}

%gwic-menu-btn-icon {
  position: relative;
  font-size: rem(40px);
  line-height: rem(18px);
  top: -1px;
  color: $white;

  &.nsw-icon {
    height: rem(16px);
    width: rem(16px);
    margin: 1px 0;
  }
}

%gwic-menu-button-sub {
  @include font-stack('heading');
  font-size: rem(12px);
}
/* stylelint-enable */

.gwic-header-btn {
  @extend %gwic-menu-btn;
  
  &__icon {
    @extend %gwic-menu-btn-icon;
  }

  &__sub {
    @extend %gwic-menu-button-sub;
  }
}

.gwic-header-btn--search {
  @include breakpoint('lg') {
    .gwic-header-btn__sub {
      @include sr-only;
    }

    .gwic-header-btn__icon {
      font-size: rem(34px);

      &.nsw-icon {
        width: rem(24px);
        height: rem(24px);
        margin-bottom: 0;
      }
    }
  }
}

.gwic-search {
  &__area {
    @include break-out(absolute);
    background: $light10;
    top: 0;

    // @include breakpoint('lg') {
    //   @include break-out-reset;
    //   position: absolute;
    //   width: rem(394px);
    //   right: rem(16px);
    //   top: 50%;
    //   transform: translateY(-50%);
    // }
  }

  &__input {
    @include font-stack;
    @include nsw-font-size('md');
    background: none;
    padding: rem(8px) rem(72px) rem(8px) rem(16px);
    color: $dark80;
    width: 100%;
    appearance: none;
    border: 0;
    height: rem(72px);

    &:focus {
      @include nsw-focus($offset: false);
    }

    // @include breakpoint('lg') {
    //   @include nsw-spacing(padding, none md);
    //   @include border-radius;
    //   height: rem(50px);
    //   padding: 0 rem(16px);
    // }
  }

  &__submit {
    position: absolute;
    top: 0;
    right: 0;
    height: rem(50px);
    width: rem(50px);
    border: 0;
    appearance: none;
    background: none;
    cursor: pointer;
  }

  &__search-icon {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    font-size: rem(34px);
    right: rem(7px);
    margin-top: -1px;
    display: none;

    // @include breakpoint('lg') {
    //   display: inline-block;
    // }

    &.nsw-icon {
      width: rem(24px);
      height: rem(24px);
      right: rem(12px);
      margin-top: 0;
    }
  }

  &__close-btn {
    width: rem(48px);
    height: rem(48px);
    background: none;
    border: 0;
    padding: 0;
    text-align: center;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: rem(16px);

    &:hover {
      @include nsw-hover;
    }

    &:focus {
      @include nsw-focus;
    }
  }

  &__close-icon {
    font-size: rem(40px);

    &.nsw-icon {
      width: rem(24px);
      height: rem(24px);
    }
  }
}
