.nsw-section--white,
.nsw-section,
.nsw-section--light-10,
.nsw-section--light-20,
.nsw-section--light-40 {
  padding: rem($grid-gutters*2) 0;

  @include breakpoint('md') {
    padding: rem($grid-gutters*4) 0;
  }
}

.nsw-section--short {
  padding: 0;

  @include breakpoint('md') {
    padding: rem($grid-gutters*2) 0;
  }
}

.nsw-section--image {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;

  @include breakpoint('md') {
    padding: rem($grid-gutters*4) 0;
  }
}

.nsw-section--white {
  background-color: $white;
}

.nsw-section--light-10 {
  background-color: $light10;
}

.nsw-section--light-20 {
  background-color: $light20;
}

.nsw-section--light-40 {
  background-color: $light40;
}

.nsw-section--dark-60 {
  background-color: $dark60;
}

.nsw-section--dark-70 {
  background-color: $dark70;
}

.nsw-section--dark-80 {
  background-color: $dark80;
}

.nsw-section--box {
  @include border-radius;
  border: 1px solid $light20;
  padding: rem(24px);
}

.nsw-section--no-padding {
  padding: 0;
}

.nsw-section--hero-height-60 {
  @include breakpoint('lg') {
    min-height: 60vh;
  }
}
